import '../../App.css';
import React from 'react';
import Grid from '@material-ui/core/Grid';
import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';
import {Link} from 'react-router-dom';
import {IconButton} from "@material-ui/core";
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import MenuIcon from '@material-ui/icons/Menu';
import 'date-fns';
import SectionBlockVolume from "../../components/SectionBlockVolume";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: "100%",
        backgroundColor: 'rgb(255,255,255)',
    },

    hrUp: {
        borderBottom: "1px solid rgba(255, 255, 255, 0.3)",
        border: 0,
        width: "85%",
    },

    hrDown: {
        margin: "20px auto",
        borderTop: "1px solid rgba(0, 0, 0, 0.1)",
        borderBottom: "1px solid rgba(255, 255, 255, 0.3)",
        border: 0,
        height: 0,
        maxWidth: "45%",
        [theme.breakpoints.down('600')]: {
            maxWidth: "90%",
        },
        [theme.breakpoints.up('xs')]: {
            maxWidth: "80%",
        },
        [theme.breakpoints.up('md')]: {
            maxWidth: "60%",
        },
        [theme.breakpoints.up('lg')]: {
            maxWidth: "40%",
        },
    },

    UpperBlock: {
        background: 'rgb(22,111,255)',
        padding: 15,
        borderBottomRightRadius: 50,
    },

    header: {
        fontSize: 24,
        fontWeight: "bold",
        color: 'rgb(255,255,255)',
    },

    title: {
        fontSize: 15,
        fontWeight: "bold",
        textAlign: "left",
    },

    subtitle: {
        fontSize: 12,
        fontWeight: "bold",
        color: 'rgb(113,113,113)',
        textAlign: "left",
    },

    date_text: {
        fontSize: 12,
        color: 'rgb(113,113,113)',
        textTransform: 'uppercase',
    },

    date_value: {
        fontSize: 18,
        color: 'rgb(113,113,113)',
        fontWeight: "bold",
    },

}));

function App({works}) {

    const classes = useStyles();

    return (
        <div className="App">
            <header className="App-header">
                <React.Fragment>
                    <div className={classes.root}>
                        <Grid container direction="column" className={classes.UpperBlock}>
                            <Grid container justifyContent="space-between">
                                <Grid item>
                                    <Link to="/">
                                        <IconButton>
                                            <ArrowBackIosIcon fontSize="large" style={{ color: 'rgb(159,191,244)' }}/>
                                        </IconButton>
                                    </Link>
                                </Grid>
                                <Grid item>
                                    <IconButton aria-label="delete">
                                        <MenuIcon fontSize="large" style={{ color: 'rgb(159,191,244)' }}/>
                                    </IconButton>
                                </Grid>
                            </Grid>
                            <hr className={classes.hrUp}/>
                            <Grid item><div className={classes.header}>Набор работ на Июль 2021</div></Grid>
                        </Grid>
                        <Grid container justifyContent="center" alignItems="center" direction="column">
                            {works.map((obj) => {
                                return (
                                    <SectionBlockVolume
                                        title={obj.title}
                                        subtitle={obj.subtitle}
                                        progress={obj.progress}
                                        date_begin={obj.date_begin}
                                        date_end={obj.date_end}
                                        quantity={obj.quantity}
                                        imgsArray={obj.imgsArray}
                                    />
                                )
                            })}
                        </Grid>
                    </div>
                </React.Fragment>
                <CssBaseline />
            </header>
        </div>
    );
}
export default App;
