import './App.css';
import React from 'react';
import HomePage from "./pages/HomePage";
import VolumeWorkPage from "./pages/VolumeWorkPage";
import OthersVolumeWorkPage from "./pages/OthersVolumeWorkPage";
import StatVolumeWorkPage from "./pages/StatVolumeWorkPage";
import {
    Switch,
    Route,
} from "react-router-dom";
import {works, profile, infoMain, infoWorks} from "./data";

function App() {

    return (
        <Switch>
            <Route exact path="/">
                <HomePage profile={profile} infoMain={infoMain}/>
            </Route>
            <Route exact path="/VolumeWorkPage">
                <VolumeWorkPage infoWorks={infoWorks}/>
            </Route>
            <Route exact path="/OthersVolumeWorkPage">
                <OthersVolumeWorkPage works={works}/>
            </Route>
            <Route exact path="/StatVolumeWorkPage">
                <StatVolumeWorkPage infoWorks={infoWorks}/>
            </Route>
        </Switch>
    );
}

export default App;