import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import LinearProgress from '@material-ui/core/LinearProgress';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

const LinearProgressWithLabel = withStyles(() => ({
    root: {
        height: 6,
        borderRadius: 5,
    },
    colorPrimary: {
        backgroundColor: 'rgba(182,188,226,0.6)',
    },
    bar: {
        borderRadius: 5,
    },
}))(LinearProgress);

const useStyles = makeStyles({
    root: {
        width: '100%',
    },

    red: {
        "& .MuiLinearProgress-barColorPrimary": {
            backgroundColor: 'red',

        },
    },

    green: {
        "& .MuiLinearProgress-barColorPrimary": {
            backgroundColor: 'green',

        },
    },

    orange: {
        "& .MuiLinearProgress-barColorPrimary": {
            backgroundColor: 'orange',
        },
    },
});

export default function LinearWithValueLabel({progress}) {
    const classes = useStyles();

    return (
        <div className={classes.root}>

            {/*<LinearProgressWithLabel className={progress > 40 ? classes.green : classes.red} value={progress} />*/}

            <Box display="flex" alignItems="center">
                <Box width="100%" mr={1}>
                    <LinearProgressWithLabel
                        className={(() => {
                            if (progress < 40) {
                                return (classes.red)
                            }
                            else if (progress < 75) {
                                return (classes.orange)
                            }
                            else {
                                return (classes.green)
                            };
                        })()
                        }
                        variant="determinate"
                        value={progress}
                    />
                </Box>
                <Box minWidth={35}>
                    <Typography variant="body2" color="textSecondary">{`${Math.round(
                        progress,
                    )}%`}</Typography>
                </Box>
            </Box>
        </div>
    );
}



