import React from 'react';
import Grid from "@material-ui/core/Grid";
import {makeStyles} from "@material-ui/core/styles";
import { useHistory } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },

    title : {
        fontSize: "14px",
        textTransform: 'uppercase',
        fontWeight: 'bold',
        color: 'rgba(22,111,255,0.84)',
    },

    subtitle : {
        fontSize: "12px",
        color: 'rgba(22,111,255,0.84)',
        textTransform: 'none',
    },

    value : {
        fontSize: "18px",
        fontWeight: 'bold',
        color: 'rgb(255,255,255)',
    },

    block: {
        background: 'rgb(229,239,255)',
        borderRadius: "15px",
        padding: theme.spacing(1.3),
        margin: theme.spacing(1),
        "&:hover": {
            cursor: 'pointer',
            background: 'rgb(218,233,255)',
        },
    },
    block_icon: {
        background: 'rgb(159,191,244)',
        borderRadius: "10px",
        display: 'flex',
        alignItems: "center",
        justifyContent: "center",
        [theme.breakpoints.between('600', '950')]: {
            maxWidth: "15%",
        },
        [theme.breakpoints.up('950')]: {
            maxWidth: "10%",
        },
        margin: theme.spacing(0.5),
    },

    block_title_subtitle: {
        textAlign: "left",
    },

    block_value: {
        background: 'rgb(47,128,237)',
        borderRadius: "17px",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        marginLeft: "auto",
        [theme.breakpoints.between('600', '950')]: {
            maxWidth: "25%",
        },
        [theme.breakpoints.up('950')]: {
            maxWidth: "15%",
        },
        margin: theme.spacing(1),
    },

}));

function SectionBlock({ icon, title, subtitle, value, link }) {

    const classes = useStyles();
    const history = useHistory();

    function goToPage() {
        history.push(link);
    }

    return (
        <Grid container spacing={3} xs={12} sm={6} className={classes.block} onClick={goToPage}>
            <Grid item className={classes.block_icon} xs={2} sm={2} >
                <img src={icon}/>
            </Grid>
            <Grid item className={classes.block_title_subtitle} xs={5} sm={6}>
                <div className={classes.title}>{title}</div>
                <div className={classes.subtitle}>{subtitle}:</div>
            </Grid>
            <Grid item className={classes.block_value} xs={4} sm={3}>
                <div className={classes.value}>{value}</div>
            </Grid>
        </Grid>
    );
}

export default SectionBlock;