import React, {useEffect,useState} from 'react';
import Grid from "@material-ui/core/Grid";
import {makeStyles} from "@material-ui/core/styles";
import { useHistory, Link } from "react-router-dom";
import ProgressBar from "../ProgressBar"
import Avatar from '@material-ui/core/Avatar';
import AvatarGroup from '@material-ui/lab/AvatarGroup';
import AccessTimeIcon from '@material-ui/icons/AccessTime';

const useStyles = makeStyles((theme) => ({

    bar: props => ({
        borderRadius: 8,
        backgroundColor: props.color,
    }),

    root: {
        flexGrow: 1,
    },

    blockTitle : {
        fontSize: "15px",
        fontWeight: 'bold',
        textAlign: "left",
    },

    blockSubtitle : {
        fontSize: "12px",
        color: 'rgb(113,113,113)',
        textAlign: "left",
        fontWeight: 'bold',
        paddingBottom: 2,
    },

    value : {
        fontSize: "18px",
        fontWeight: 'bold',
        color: 'rgb(255,255,255)',
    },

    blockHeader: {
        background: 'rgb(229,239,255)',
        borderRadius: "15px",
        padding: theme.spacing(2),
        marginTop: theme.spacing(2),
        "&:hover": {
            cursor: 'pointer',
            background: 'rgb(218,233,255)',
        },
    },

    blockBottom: {
        borderRadius: "10px",
        fontSize: "13.5px",
        fontWeight: "bold",
        textAlign: "left",
    },

    employee: {
        objectFit: "cover",
        width: 30,
        height: 30,
        borderRadius: "50%",
    },

    hr: {
        borderBottom: "1px solid rgba(255, 255, 255, 0.3)",
        border: 0,
        width: "100%",
        backgroundColor: 'rgb(164,164,164)',
    },

    avatarGroup: {
        "& .MuiAvatar-root": {
            fontSize: 13,
            width: theme.spacing(4),
            height: theme.spacing(4),
        },
    },

    dateBlock: {
        display: 'flex',
        alignItems: "center",
        justifyContent: "center",
        marginRight: theme.spacing(0.75),
    },

    timeIcon: {
      fontSize: 17,
    },

    block: {
      backgroundColor: "aqua",
    },

}));

function SectionBlockVolume({title, subtitle, date_begin, date_end, progress, quantity, imgsArray}) {
    const classes = useStyles();
    const history = useHistory();
    const [emplArray, setEmplArray] = useState([]);

    useEffect(() => {
        let bufArray = [];
        for (let i = 0; i < quantity; i++) {
            //setEmplArray(emplArray.push(<Avatar src={imgsArray[i]}/>));
            bufArray[i] = <Avatar src={imgsArray[i]}/>
        }
        setEmplArray(bufArray);
    },[]);

    function goToPage() {
        history.push("StatVolumeWorkPage");
    }

    return (
        <Grid container className={classes.blockHeader} xs={11} sm={9} md={8} lg={4} onClick={goToPage}>
            <Grid item className={classes.blockTitle} xs={11} lg={10}>{title}</Grid>
            <Grid item className={classes.blockSubtitle} xs={11} lg={7}>{subtitle}</Grid>
            <ProgressBar progress={progress}/>
            <hr className={classes.hr}/>
            <Grid container className={classes.blockBottom} justifyContent="space-between">
                <Grid container alignItems="center" xs={9}>
                    <Grid item className={classes.dateBlock}>
                        <AccessTimeIcon className={classes.timeIcon}/>
                    </Grid>
                    <Grid item>{date_begin} – {date_end}</Grid>
                </Grid>
                <Grid item>
                    <AvatarGroup max={3} className={classes.avatarGroup}>
                        {emplArray}
                    </AvatarGroup>
                </Grid>
            </Grid>
        </Grid>
    );
}

export default SectionBlockVolume;